.cardWrapper ul {
	display: flex;
}

.cardWrapper ul li {
	flex: 1 1 0px;
}

.imageWrapper {
	padding: 16px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.image {
	/* width: 100%; */
	border: solid 1px black;
	box-shadow: 0 0 3px var(--snow-transparent);
}

@media only screen and (max-width: 940px) {
	.imageWrapper {
		padding: 12px;
	}
}

@media only screen and (max-width: 700px) {
	.imageWrapper {
		padding: 8px;
	}
}

@media only screen and (max-width: 500px) {
	.imageWrapper {
		padding: 6px;
	}
}

@media only screen and (max-width: 316px) {
	.imageWrapper {
		padding: 4px;
	}
}
